import axios from "axios";
import qs from "qs"

// const baseUrl = "/api"//
const baseUrl="";


//
export const requestLogin = (data) => {
    return axios({
        url: baseUrl + "/login",
        method: "post",
        data: qs.stringify(data)
    })
}

//
export const requestIndex = () => {
    return axios({
        url: baseUrl + "/findDevice",
        method: "get",
    })
}


